import React from 'react'
import { Line } from 'react-chartjs-2'

export const MyChart = (props) => {

    var t1 = props.temp_1
    var t2 = props.temp_2
    var t3 = props.temp_3
    var t4 = props.temp_4
    const labels = [" ", " ", " ", " "]
    const data = {
        labels: labels,
        datasets: [{
            label: 'Temperature every 8 hrs',
            backgroundColor: 'rgba(54, 162, 235, 0.3)',
            fill: true,
            borderColor: 'rgb(54, 162, 235)',
            borderWidth: 3,
            data: [t1, t2, t3, t4],

        }]
    };
    const options = {
        type: 'line',
        maintainAspectRatio: false,
        plugins: {
            legend: {
                display: false
            }
        },
        tooltips: {
            callbacks: {
                label: function (tooltipItem) {
                    return tooltipItem.yLabel;
                }
            }
        },
        elements: {
            line: {
                tension: 0.3
            }
        },
        scales: {
            x: {
                drawBorder: false,
                grid: {
                    display: false,
                },
                title: {
                    display: false,
                },
            },
            y: {
                beginAtZero: true,
                ticks: {
                    callback: function (value, index) {
                        return ' '
                    }
                },
                grid: {
                    display: false,
                    drawOnChartArea: false,
                    drawBorder: false,
                },
            },
            yAxes: {
                display: false,
            }
        },
    };
    return (
        <div className="container-fluid">
            <h5>Temperature in {props.name}</h5>
            <div className="chart">
                <Line data={data} options={options} />
            </div>
        </div>
    )
}
