import './App.css';
import React, { useEffect, useState } from 'react'
import axios from 'axios';

import { Body1 } from './MyComponents/Body1';
import { MyChart } from './MyComponents/MyChart';
import { WeatherForecast } from './MyComponents/WeatherForecast';

// import { Body } from './MyComponents/Body';

function App() {

  let monthsList = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Aug', 'Oct', 'Nov', 'Dec'];

  var d = new Date();
  var date = d.getDate();
  var month = monthsList[d.getMonth()];
  const [val, setItems] = useState([]);
  const [se, setSearch] = useState('mumbai');


  useEffect(() => {
    axios.get(`https://api.weatherapi.com/v1/forecast.json?key=e57c938000904073999113539211309 &q=${se}&days=5&aqi=no&alerts=no`)
      .then(res => {
        const { name } = res.data.location;
        const { temp_c, humidity, wind_kph } = res.data.current;
        const { text, icon } = res.data.current.condition;
        const { temp_f } = res.data.forecast.forecastday[0].hour[0];
        const con_icon_1 = res.data.forecast.forecastday[0].day.condition.icon;
        const con_icon_2 = res.data.forecast.forecastday[1].day.condition.icon;
        const con_icon_3 = res.data.forecast.forecastday[2].day.condition.icon;
        const avgtemp_c_1 = res.data.forecast.forecastday[0].day.avgtemp_c;
        const avgtemp_c_2 = res.data.forecast.forecastday[1].day.avgtemp_c;
        const avgtemp_c_3 = res.data.forecast.forecastday[2].day.avgtemp_c;
        const temp_1 = res.data.forecast.forecastday[0].hour[5].temp_c;
        const temp_2 = res.data.forecast.forecastday[0].hour[11].temp_c;
        const temp_3 = res.data.forecast.forecastday[0].hour[17].temp_c;
        const temp_4 = res.data.forecast.forecastday[0].hour[23].temp_c;

        const val = {
          name,
          temp_c,
          humidity,
          text,
          icon,
          wind_kph,
          temp_f,
          con_icon_1,
          con_icon_2,
          con_icon_3,
          avgtemp_c_1,
          avgtemp_c_2,
          avgtemp_c_3,
          temp_1,
          temp_2,
          temp_3,
          temp_4
        }
        setItems(val);
      }).catch(err => {
        console.log(err)
      })
  }, [se])

  const searchB = (title) => {
    setSearch(title);
  }

  const Sdata = [
    {
      title: "Today",
      con_icon: val.icon,
      avgtemp_c: val.temp_c
    },
    {
      title: month + ' ' + (date + 1),
      con_icon: val.con_icon_1,
      avgtemp_c: val.avgtemp_c_1
    },
    {
      title: month + ' ' + (date + 2),
      con_icon: val.con_icon_2,
      avgtemp_c: val.avgtemp_c_2
    },
    {
      title: month + ' ' + (date + 3),
      con_icon: val.con_icon_3,
      avgtemp_c: val.avgtemp_c_3
    }
  ]


  return (
    <div>
      <div className="bg_card">
        <div className="container p-5">
          <div className="row">
            <div className="col-sm">
              <Body1
                search={searchB}
                icon={val.icon}
                temp={val.temp_c}
                w_name={val.text}
                humidity={val.humidity}
                w_speed={val.wind_kph}
              />
            </div>
            <div className="col-sm mt-10 pl-3">
                <MyChart
                  name={val.name}
                  temp_1={val.temp_1}
                  temp_2={val.temp_2}
                  temp_3={val.temp_3}
                  temp_4={val.temp_4}
                />
                <WeatherForecast
                  Sdata={Sdata}
                />
            </div>
          </div>
        </div>
      </div>


      {/* 
      <Body
        w_speed={val.wind_kph}
        temp={val.temp_c}
        humidity={val.humidity}
        w_name={val.text}
        icon={val.icon}
        search={searchB}
        name={val.name}
        con_icon_1={val.con_icon_1}
        con_icon_2={val.con_icon_2}
        con_icon_3={val.con_icon_3}
        avgtemp_c_1={val.avgtemp_c_1}
        avgtemp_c_2={val.avgtemp_c_2}
        avgtemp_c_3={val.avgtemp_c_3}
        temp_1={val.temp_1}
        temp_2={val.temp_2}
        temp_3={val.temp_3}
        temp_4={val.temp_4} /> */}
    </div>
  );
}


export default App;
