import React from 'react'

export const WeatherInfo = (props) => {
    return (
        <div className="container my-5">
            <div className="row">
                <div className="col-sm text-center">
                    <h3>Humidity</h3>
                    <h5>{props.humidity}%</h5>
                </div>
                <div className="col-sm text-center">
                    <h3>Wind Speed</h3>
                    <h5>{props.w_speed} km/h</h5>
                </div>
            </div>
        </div>
    )
}
