import React, { useState } from 'react'


export const Search = (props) => {

    const [title, setTitle] = useState("");

    const submit = (e) => {
        e.preventDefault();
        if (!title) {
            alert("Please enter a city to continue")
        }
        props.search(title);
    }
    return (
        <div>
            <form className="d-flex" onSubmit={submit}>
                <h4>Your City</h4>
                <input className="form-control  mx-2 w-50" type="search" placeholder="Search" aria-label="Search" value={title} onChange={(e) => { setTitle(e.target.value) }} id="title" />
                <button className="btn btn-outline-primary btn-sm" type="submit">Search</button>
            </form>
        </div>
    )
}
