import React from 'react'
import { Card } from './Card';

export const WeatherForecast = (props) => {

    return (
        <div class="card-group">
            {props.Sdata.map((val, ind) => {
                return (
                    <Card
                        key={ind}
                        img={val.con_icon}
                        title={val.title}
                        desc={val.avgtemp_c}
                    />
                );
            })}
        </div>
    )
}
